<template>
  <div id="my">
    <button id="logout" @click="goLogin">退出登录</button>
  </div>
</template>

<script>
  export default{
    data(){
      return{

      }
    },
    methods:{
      goLogin:function(){
        this.$message({
          type:'success',
          message:'退出成功',
          center:true
        });
        this.success();
      },
      success:function(){
        this.$router.push({path:'/login'})
      }
    },
    created:function(){

    },
    mounted:function(){
    }
  }
</script>

<style lang="scss" scoped>
  #my{
    height:100%;
    background:#f2f2f2;
    #logout{
      width:90%;
      margin: 0 auto;
      background: #2992d9;
      color:#fff;
      padding:.5rem 0;
      border-radius:5px;
      margin-top: 10rem;
      box-shadow:none;
      border:none;
      font-size:.8rem;
      font-weight:bold;
    }
  }
</style>


