<template>
	<div id="login" :style="img">
		<p class="font">Welcome</p>
		<p class="font">Welcome to the green world where you can find hopeful and vitality</p>
		<input class="phone inp" v-model="phoneNumber" placeholder="请输入11位手机号"></input>
		<input class="pass inp" type="password" v-model="passWord" placeholder="请输入密码"></input>
		<el-button class="inp" @click.native="Login" type="primary">Login</el-button>
		<el-row>
			<el-col @click.native="goRegister" :span="8">注册用户</el-col>
			<el-col :span="8">|</el-col>
			<el-col @click.native="goForget" :span="8">忘记密码</el-col>
		</el-row>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				phoneNumber:'',
				passWord:'',
				img:{background:'url(' + require('../../static/img/pubu.jpg') + ')'},
			}
		},
		methods:{
			Login:function(){
				const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
				const pass = sessionStorage.getItem('password',this.passWord);
				const user = sessionStorage.getItem('username',this.username);
				console.log(pass);
				if (this.phoneNumber=='') {
					this.$message({
						message:'请输入手机号',
						type:'error',
						center:true
					});
				}
				else if(reg.test(this.phoneNumber)!=true){
					this.$message({
						message:'请输入正确手机号',
						type:'error',
						center:true
					});
				}
				else if (this.passWord=='') {
					this.$message({
						message:'请输入密码',
						type:'error',
						center:true
					});
				}
				else if (this.phoneNumber!=user) {
					this.$message({
						message:'没有此用户',
						type:'error',
						center:true,
					});
				}
				else if(this.passWord!=pass){
					this.$message({
						message:'密码错误',
						type:'error',
						center:true
					});
				}
				else if(this.passWord==pass&&this.phoneNumber==user){
					this.$message({
						message:'登录成功',
						type:'success',
						center:true,
						duration:5000
					});
					this.success();
					
				}

			},
			goRegister:function(){
				this.$router.push({path:'/register'})
			},
			goForget:function(){
				this.$router.push({path:'/forget'})
			},
			success:function(){
				this.$router.push({path:'/index/homepage'})
			}
		}
	}
</script>

<style lang="scss" scoped>
	#login{
		height:100%;
		// background-image: url('../../static/img/pubu.jpg');
		p:first-child{
			font-size:2.6rem;
			font-weight:bold;
			padding-top: 6rem;
		}
		p:nth-child(2){
			font-size:1rem;
			font-weight:bold;
			color:#f29b52;
			padding-top: 2rem;
		}
		.font{
			color:#fff;
		}
		.phone{
			margin-top: 4rem !important;
			background: #f2f2f2;
		}
		.pass{
			background: #f2f2f2;
		}
		.inp{
			text-align:center;
			font-weight:bold;
			opacity: .9;
			padding:10px;
			box-shadow:none;
			border:none;
			width:90%;
			margin: 0 auto;
			border-radius:16px !important;
			margin-top: 2rem;
		}
		.el-row{
			margin-top: 6rem;
			color:#fff;
			font-weight:bold;
			font-size:1rem;
			padding:0 3rem;
		}
	}
</style>







